<template>
  <div class="overview">
        <div v-if="data.type === 'course'" class="overview_title">
            {{ data.title }}
        </div>
        <div v-else class="chapter" @click="$router.back()">
            <img src="@/assets/course/arrow-left-chapter.svg" alt="arrow-left">
