        </div>
        <div class="overview_list">
            <div v-for="(stage, index) in data.menu">
                <div 
                    class="overview_item" 
                    :class="{'active': currentStage.index === index && currentStage.type === 'stage'}"
                    v-if="stage.type !== 'module'"
                    @click="onChangeStage(index, 'stage')"
                    >
                    <div class="name">
                        {{ stage.name }}
                    </div>
                </div>
                <template v-else>
                    <div
                        v-for="(stageModule, moduleIndex) in stage.modules"
                        class="overview_item" 
                        :class="{'active': currentStage.index === moduleIndex && currentStage.type === 'module' }"
                        @click="onChangeStage(moduleIndex, 'module')"
                    >
                        <div class="name">
