                                {{ chapter.title }}
                            </div>
                        </div>
                    </div>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
export default {
    props: ['data', 'currentStage'],

    data() {
        return {
            currentTopicIndex: 0
        }
    },

    watch: {
        'currentStage.index'(newValue) {
            this.changeRouteQuery(this.currentStage.index, this.currentStage.type, this.currentStage.chapterIndex)
            if (this.data.topics) {
                this.currentTopicIndex = newValue
            }
        } 
    }, 

    methods: {
        toggleTopic(topicIndex) {
            if (this.currentTopicIndex === topicIndex) {
                this.currentTopicIndex = -1
            } else {
                this.currentTopicIndex = topicIndex
            }
        },
        onChangeStage(index, type, chapterIndex = null) {
            this.changeRouteQuery(index, type, chapterIndex)
            this.$emit('currentMenu', {index: index, type: type, chapterIndex: chapterIndex})
        },

        changeRouteQuery(index, type, chapterIndex = null) {
            let query = {
                page: index,
                type: type,
            }
            if (chapterIndex) {
                query.chapter = chapterIndex
            }
            this.$router.replace({ query: query}).catch(()=>{})
        }
    }
}
</script>

