<template>
    <div>
        <div class="module">
            <div
              class="module_item"
              :class="{
                active: active,
                success: success,
                forbidden: forbidden,
                fail: fail
              }"
            >
                <div class="top-hint duration" v-if="!fail">60 минут</div>
                <div class="top-hint result-hint" v-if="fail">
                  Результат:
                </div>
                <div class="module_item_status">
                    <div>
                        <img v-if="active" src="@/assets/course/rotation-icon.svg" alt="">
                        <img v-if="success" src="@/assets/course/check.svg" alt="">
                        <img v-if="forbidden" src="@/assets/course/forbidden-icon.svg" alt="">
                        <img v-if="fail" src="@/assets/course/close-icon.svg" alt="">
                    </div>
                </div>
                <div class="module_item_info">
                    <div class="name">
                        {{ quiz['title'] }}
                    </div>
                    <div class="buttons" v-if="quiz['available']">
                        <button class="btn" v-if="success">Пройдено</button>
                        <button class="btn" v-if="fail">Не пройдено</button>
                        <button class="btn" v-if="!fail && canStart">Начать</button>
                    </div>
                    <div class="passing_score" v-if="fail">
                      Проходной балл: {{ quiz['passing_score'] }}
                    </div>
                    <button class="retry" v-if="fail && quiz['can_refresh']">Пройти еще раз</button>
                    <div class="result" v-if="success || fail">
                      {{ quiz['percent'] }}%
                    </div>
                    <div v-if="!quiz['available']" class="info">
                        <div class="info_text">
                            Вы не можете начать, пока не закончите предыдущую тему
                        </div>
                        <img class="info_icon" src="@/assets/course/info-icon.svg" alt="">
                    </div>
                </div>
            </div>
        </div>
        <div class="final-description">
            <div class="hint" v-html="quiz['quiz_rules']"></div>
        </div>
    </div>
</template>

<script>
export default {
  props: {
    quiz: {
      type: Object,
      default: {}
    }
  },
  computed: {
    active() {
      return this.quiz['available'] && !this.quiz['user_answered'];
    },
    success() {
      return this.quiz['available'] && this.quiz['user_result'] >= this.quiz['passing_score'];
    },
    forbidden() {
      return !this.quiz['available']
    },
    fail() {
      return this.quiz['user_answered'] && (this.quiz['user_result'] < this.quiz['passing_score'])
    },
    canStart() {
      return this.quiz['user_result'] < this.quiz['passing_score'];
    }
  }
}
</script>

