<template>
    <div class="module">
        <div
            v-for="(item, index) in courseData"
            class="module_item"
            :class="{'active': item.progress >= 0 && !item.score, 'noaccess': !item.available}"
            @click="item.available ? openChapter(item) : ''"
            >
            <div class="module_item_status">
                <div v-if="item.score">
                ✌️
                </div>
                <div v-else-if="item.progress >= 0">
                    <img src="@/assets/course/rotation-icon.svg" alt="">
                </div>
                <div v-else-if="!item.available">
                    <img src="@/assets/course/close-icon.svg" alt="">
                </div>
            </div>
            <div class="module_item_info">
                <div class="name">
                    {{ item.name }}
                </div>
                <div class="buttons" v-if="item.available">
                    <template v-if="item.progress >= 0 && item.result === 0">
                        <div class="process">
                            <div class="process_finished" :style="'width:' + item.progress + '%'">
                                В процессе
                            </div>
                            <div class="process_score">
                                {{ item.progress }}%
                            </div>
                        </div>
                    </template>
                    <template v-else>
                        <button class="btn">
                            Пройдено
                        </button>
                        <button class="btn success" v-if="item.score">
                            Результат:  {{item.score}}%
                        </button>
                    </template>
                </div>
                <div class="info" v-else>
                    <div class="info_text">
                        Вы не можете начать, пока не закончите предыдущую тему
                    </div>
                    <img class="info_icon" src="@/assets/course/info-icon.svg" alt="">
                </div>
            </div>
        </div>
        <div 
            class="module_item testing" 
            :class="{
                'active': testing.available && testing.score === 0, 
                'success': testResult, 
                'fail': testing.score > 0 && !testResult, 
                'noaccess': !testing.available
            }">
            <div class="module_item_corner" v-if="!testing.available"></div>
            <div class="module_item_corner" v-else-if="testing.score === 0">
                    <img src="@/assets/course/clock-icon.svg" alt="">
                {{ testing.duration }} минут
            </div>
            <div class="module_item_corner" v-else>
                Результат: 
            </div>
            <div class="module_item_status">
                <div v-if="!testing.available">
                    <img src="@/assets/course/close-icon.svg" alt="">
                </div>
                <div v-else-if="testing.score === 0">
                    <img src="@/assets/course/rotation-icon.svg" alt="">
                </div>
                <div v-else-if="testResult">
                    <img src="@/assets/course/success-icon.svg" alt="">
                </div>
                <div v-else>
                    <img src="@/assets/course/close-icon.svg" alt="">
                </div>
            </div>
            <div class="module_item_info">
                <div class="name">
                    Тестирование
                </div>
                <div class="buttons" v-if="testing.available">
                    <button class="btn" v-if="testing.score === 0">
                        Начать
                    </button>
                    <button class="btn" v-else-if="testResult">
                        Пройдено
                    </button>
                    <button class="btn" v-else>
                        Не пройдено
                    </button>
                    <div class="test-result" v-if="testing.score > 0">
                        <div class="test-result_item">
                            Пройдено: {{ testing.passedDate }}
                        </div> 
                        <span></span>
                        <div class="test-result_item">
                            Проходной балл: {{ testing.minScore }}%
                        </div>
                    </div>
                    <div class="retry" v-if="testing.score > 0 && testing.score < testing.minScore">
                        Пройти еще раз
                    </div>
                    <div class="result" v-if="testing.score > 0">
                        {{ testing.score }}%
                    </div>
                </div>
                <div class="info" v-else>
                    <div class="info_text">
                        Вы не можете начать, пока не закончите предыдущую тему
                    </div>
                    <img class="info_icon" src="@/assets/course/info-icon.svg" alt="">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  props: ['courseData', 'slug', 'testing'],

  computed: {
    testResult() {
        return this.testing?.score >= this.testing?.minScore
    }
  },

  methods: {
    openChapter(chapter) {
      this.$router.push({name: 'course-chapter', params: {
        slug: this.slug,
        chapterId: chapter.id
      }})
    }
  }
}
</script>

